import React, { Component } from 'react';
import './Key.css';



class Key extends React.Component {
    constructor(props) {
        super(props);
        this.handleClick = this.handleClick.bind(this);
    }

    playSound(target){
        this.sound = new Audio();
        this.sound.src = 'https://pianonotesforuse.s3.us-east-2.amazonaws.com/' + this.props.note+ '.mp3';
        this.sound.load();
        const promise = this.sound.play();
        if (promise !== undefined) { // On older browsers play() does not return anything, so the value would be undefined.
            promise
                .then(() => {
                    // Audio is playing.
                })
                .catch(error => {
                    console.log(error);
                });
        }
    }
    keyToPosition(noteName) {
        var groupOffset;
        var offset; 
    /* If 3 digits then black, if 2 digits then white; */
    if (noteName.length === 2) {
        groupOffset = parseInt(noteName.substring(1)) * 7 * 1.4 + 6;
        /* parse for white */
        switch(noteName.substring(0, 1)){
            case 'A':
                offset = groupOffset + 7.0; 
                break;
            case 'B':
                offset = groupOffset + 8.4; 
                break;
            case 'C':
                offset = groupOffset; 
                break;
            case 'D':
                offset = groupOffset + 1.4;
                break;
            case 'E':
                offset = groupOffset + 2.8; 
                break; 
            case 'F':
                offset = groupOffset + 4.2; 
                break;
            case 'G':
                offset = groupOffset + 5.6;
                break;
            default:
                offset = 0; 
        }
        return 'calc(' + offset + 'vw)';
    } else {
        groupOffset = parseInt(noteName.substring(2)) * 7 * 1.4 + 6;
        /* parse for black */
        switch (noteName.substring(0, 1)) {
            case 'D':
                offset = groupOffset + 0.9;
                break;
            case 'E':
                offset = groupOffset + 2.3;
                break;
            case 'G':
                offset = groupOffset + 5.1;
                break;
            case 'A':
                offset = groupOffset + 6.6;
                break;
            case 'B':
                offset = groupOffset + 8.0;
                break;
            default:
                offset = 0;
        }
        return 'calc(' + offset + 'vw)';
    }

}
    handleClick(e){
        this.playSound(e.target);
    }
    
    render() {
        var noteName = this.props.note;
        var leftOffset = this.keyToPosition(noteName);
            
            if(this.props.keyType==='Whitekey'){
                return (
                    <div className='white_key key' onClick={this.handleClick} note={this.props.note} style={{left:leftOffset}}>
                    </div>
                );
            } else {
                
                return (
                    <div className='black_key key' onClick={this.handleClick} note={this.props.note} style={{ left: leftOffset }}>
                    </div>
                );
            }
        
    }

}



export default Key; 




    
